*{
  border: none;
  outline: none;
  text-decoration: none;
}
::-webkit-scrollbar {
  width: 14px;
  height: 14px;
}
::-webkit-scrollbar-button {
  width: 0px;
  height: 0px;
}
::-webkit-scrollbar-thumb {
  background: #e1e1e1;
  border-radius: 5px;
}
::-webkit-scrollbar-thumb:hover {
  background: #b1a9a9;
}
::-webkit-scrollbar-thumb:active {
  background: #bebbbb;
}
::-webkit-scrollbar-track {
  background: #666666;
  border-radius: 0px;
}
::-webkit-scrollbar-track:hover {
  background: #666666;
}
::-webkit-scrollbar-track:active {
  background: #666666;
}
::-webkit-scrollbar-corner {
  background: transparent;
}
.whole-screen {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow-y: auto;
}
.loading {
  font-size: 30px;
}
.home-page {
  width: 100%;
  max-width: 1800px;
  height: calc(100% - 60px);
}
.home-page .header {
  width: 100%;
  height: 100px;
  display: flex;
  justify-content: space-around;
  align-items: flex-end;
}
.home-page .header .input-elements {
  width: 80%;
  max-width: 1800px;
  display: flex;
  justify-content: space-between;
}
.home-page .header .input-elements .text-input {
  display: flex;
}
.home-page .header .input-elements .text-input div {
  position: absolute;
  margin-top: 14px;
  z-index: 1;
}
.home-page .header .input-elements .text-input input[type="search"] {
  width: 350px;
  height: 40px;
  margin-left: -10px;
  font-size: 17px;
  border-radius: 5px;
}
.home-page .header .input-elements .search-btn{
  width: 40px;
  height: 40px;
  text-align: center;
  margin-left: 340px;
  position: absolute;
  background-color: transparent;
  cursor: pointer;
}
.home-page .header .input-elements .select-input {
  width: 160px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.home-page .header .input-elements .select-input .arrow-icon {
  position: relative;
}
.home-page .header .input-elements .select-input .select {
  width: 100%;
  height: 100%;
  cursor: pointer;
  font-size: 15px;
  border-radius: 5px;
  display: flex;
  justify-content: space-around;
  letter-spacing: 1px;
  align-items: center;
  text-transform: capitalize;
}

.home-page .header .input-elements .select-input .list {
  display: none;
}
.home-page .header .input-elements .select-input .open-list {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: absolute;
  width: 160px;
  margin-top: 220px;
  list-style: none;
  animation-name: open-list;
  animation-duration: 1s;
}
.home-page .header .input-elements .select-input .open-list li {
  width: 80%;
  height: 30px;
  margin-top: 5px;
  cursor: pointer;
  text-align: left;
  animation-name: item;
  animation-duration: 1s;
}
@keyframes open-list {
  from {
    height: 0px;
    margin-top: 10px;
  }
  to {
    margin-top: 220px;

    height: 170px;
  }
}
@keyframes item {
  from {
    height: 0px;
  }
  to {
    height: 30px;
  }
}
.home-page .containers {
  margin-top: 30px;
  width: 100%;
  max-width: 1800px;
  display: flex;
  justify-content: center;
  overflow: hidden;
}
.home-page .containers .all-elements{
  width: 250px;
  cursor: pointer;
}

.home-page .containers img {
  width: 100%;
  max-width: 250px;
  height:160px;
}
.home-page .containers .text-place{
  width: 100%;
  height: 150px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  line-height: 30px;
}
.home-page .containers .text-place div:first-child{
  width: 90%;
  display: flex;
  align-items: center;
  font-size: 25px;
}
.home-page .containers .text-place div:not(:first-child){
  width: 90%;
  height: 20px;
  font-size: 15px;
}

@media screen and (max-width:650px) {
  .home-page .header {
    height: 200px;
    display: flex;
    justify-content:center;
  }
  .home-page .header .input-elements {
    height: 100px;
    width: 90%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  .home-page .header .input-elements .text-input input[type="text"] {
    margin-left: 0px;
  }
}
