.countrypage {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}
.buttonPlace {
  width: 80%;
  height: 100px;
  display: flex;
  justify-content: flex-start;
  align-items: flex-end;
}
.buttonPlace button {
  width: 100px;
  height: 30px;
  font-size: 16px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  cursor: pointer;
  box-shadow: 0px 0px 4px 1px #1e2229;
  border-radius: 5px;
}
.countryName {
  width: 200px;
  height: 50px;
  font-size: 30px;
  font-weight: bolder;
}
.mainPage {
  width: 100%;
  max-width: 950px;
  margin-top: 100px;
  display: flex;
  justify-content: space-between;
}
.mainPage img {
  width: 400px;
  height: 280px;
}
.mainPage .list {
  width: 500px;
  display: flex;
  justify-content: space-between;
}

.mainPage .list .listOne {
  height: 140px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.mainPage .list .listTwo {
  height: 70px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.mainPage .borderCountries {
  margin-top: 60px;
  width: 500px;
  word-wrap: break-word;
  display: flex;
  flex-wrap: wrap;
}
.mainPage .borderCountries {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}
.mainPage .borderCountries .border {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}
.mainPage .borderCountries .border div {
  padding: 4px;
  margin: 6px;
  box-shadow: 0px 0px 4px 1px #1e2229;
}
@media screen and (max-width: 1000px) {
  .mainPage{
    justify-content: space-around;
  }
  .mainPage .list {
    width: 400px;
  }
  .mainPage img {
    width: 300px;
    height: 250px;
  }
  .mainPage .borderCountries {
    margin-top: 10px;
    width: 400px;
    word-wrap: break-word;
    display: flex;
    flex-wrap: wrap;
  }
}
@media screen and (max-width: 780px) {
  .mainPage {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .countryName {
    width: 200px;
  }
  .mainPage .list {
    width: 200px;
    flex-direction: column;
  }
  .mainPage .list .listTwo {
    margin-top: 20px;
  }
  .mainPage .borderCountries {
    margin-top: 10px;
    width: 200px;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
  }
  .allTexture {
    width: 300px;
    display: flex;
    flex-direction: column;
  }
}
