.loadingPlaceDark li:nth-child(-n + 3) {
  background-color: #423f3f;
  box-shadow: 0px 0px 16px 2px rgb(46, 45, 45);
}

.loadingPlaceDark li:nth-child(n + 4) {
  background-color: rgb(129, 126, 126);
  box-shadow: 0px 0px 16px 2px rgb(133, 124, 124);
}

.loadingPlaceDark li:nth-child(3) {
  animation: pulsedark 0.5s alternate infinite;
  -webkit-animation: pulsedark 0.5s alternate infinite;
