  0% {
    background-color: #423f3f;
    box-shadow: 0px 0px 16px 2px rgb(46, 45, 45);
  }
  100% {
    background-color: rgb(129, 126, 126);
    box-shadow: 0px 0px 16px 2px rgb(133, 124, 124);
  }
}
@-webkit-keyframes pulsedark {
  0% {
    background-color: #423f3f;
    box-shadow: 0px 0px 16px 2px rgb(46, 45, 45);
  }
  100% {
    background-color: rgb(129, 126, 126);
    box-shadow: 0px 0px 16px 2px rgb(133, 124, 124);
