.navbar {
  width: 100%;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.elements {
  width: 90%;
  max-width: 1800px;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 26px;
  font-weight: bolder;
  user-select: none;
}
.btn {
  cursor: pointer;
  width: 130px;
  height: 30px;
  border: none;
  outline: none;
  font-size: 17px;
}